import React, { useEffect, useState } from "react";
import { Card, Form, Button, Col, Row, FormLabel } from "react-bootstrap";
import { FiUpload } from "react-icons/fi";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { AiOutlineRight } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import IMAGES from "../../../assets/images";
import ProfileLayout from "../../../components/ProfileLayout/CompleteProfile";
import { PATH } from "../../../utils/path";
import { useForm } from "react-hook-form";
import { FieldError } from "../../../components/ErrorMessage/ErrorMessage";
import {
  getPatient,
  updatePatient,
} from "../../../app/features/patient/patient.slice";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import AWSImageUpload from "../../../app/features/bucketImageUpload/bucketImageUpload.service";
import convertBase64 from "../../../components/GenericActions";
import OTPCodeModal from "./OTPCodeModal";
import { verifyNumberOtp } from "../../../app/features/phoneVerification/phoneVerification.slice";
import {
  getAllPatientPharmacy,
  getPatientPharmacyData,
} from "../../../app/features/patientPharmacy/patientPharmacy.slice";

const PersonalInformation = () => {
  const [show, setShow] = useState(false);
  const [imageUrl, setImageUrl] = useState();
  const [loading, setLoading] = useState();

  const { getPatientData } = useSelector((state) => state.patient);
  const { isresendOtp } = useSelector((state) => state?.verifyNumber);
  const [value, setValue] = useState(getPatientData?.phoneNumber);

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleClose = () => setShow(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatient(finalData));
  }, [dispatch, user?.userId]);

  /** s3 Bucket Image Upload */
  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);
      // convertBase64(file, (url) => {
      convertBase64(file).then((url) => {
        setLoading(true);
        const fileData = {
          name: name,
          base64: url,
          fileExtension: `${ext}`,
        };
        AWSImageUpload(fileData).then((response) => {
          if (response) {
            setImageUrl(response);
            setLoading(false);
          } else {
            // notification.error({
            //   message: response?.message,
            // });
          }
        });
      });
    }
  };
  
  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  const moveToNext = () => {
    navigate(PATH.MEDICAL_HISTORY);
  };

  function onSubmit(data) {
    const finalData = {
      patientId: user?.userId,
      firstName: data?.firstName,
      lastName: data?.lastName,
      image: imageUrl?.keyName
        ? imageUrl?.keyName
        : getPatientData?.image
        ? getPatientData?.image
        : "",
      dob: data?.dob,
      genderId: +data?.genderId,
      patientLocalGP: data?.patientLocalGP,
      phoneNumber: value
        ? value
        : getPatientData?.phoneNumber
        ? getPatientData?.phoneNumber
        : "",
      currentAddress: data?.currentAddress,
      nextOfKinName: data.nextOfKinName,
      nextOfKinNumber: data.nextOfKinNumber,
      pharmacy: null,
    };
    dispatch(updatePatient({ finalData, moveToNext }));
  }
  const moveToOTPCode = () => {
    setShow(true);
  };

  function handleVerify() {
    const finalData = {
      userId: user?.userId,
      phoneNumber: value ? value : getPatientData?.phoneNumber,
    };
    dispatch(verifyNumberOtp({ finalData, moveToOTPCode }));
  }

  return (
    <div className="main-personalprofile">
      <ProfileLayout className="pt-5 ">
        <div className="mx-auto width_75">
          {getPatientData && (
            <Form onSubmit={handleSubmit(onSubmit)}>
              <Card className="width_75 mx-auto">
                <Card.Header className="bg-white py-4">
                  <h5 className="fw-semibold mb-0">Personal Information</h5>
                </Card.Header>

                <Card.Body className="px-4 px-md-5">
                  <div className="upload_pic profile_upload profileContent">
                    <img
                      alt="upload"
                      className="Profile-2 test-border"
                      src={
                        imageUrl?.baseUrl ||
                        getPatientData?.imageUrl ||
                        IMAGES.PROFILE_UPLOAD
                      }
                    />
                    <Button className="ms-sm-5 ms-0 mt-3 mt-sm-0">
                      <label
                        htmlFor="patient-pic"
                        className="text-center w-100 d-flex align-items-center"
                      >
                        <FiUpload className="fs-5 me-2" />
                        Upload
                      </label>
                      <Form.Control
                        onChange={(e) => handleChangeUpload(e)}
                        type="file"
                        id="patient-pic"
                        name="patient-pic"
                        accept="image/*"
                      />
                    </Button>
                  </div>

                  <Row className="mt-5">
                    <Col md={6}>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          First Name
                        </FormLabel>

                        <Form.Control
                          type="text"
                          name="firstName"
                          placeholder="John"
                          defaultValue={getPatientData?.firstName}
                          className="p-2"
                          {...register("firstName", {
                            required: true,
                          })}
                        />

                        {errors.firstName && (
                          <FieldError message={"This Field is Required"} />
                        )}
                      </Form.Group>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          D.O.B
                        </FormLabel>
                        <Form.Control
                          type="date"
                          name="dob"
                          defaultValue={moment(getPatientData?.dob).format(
                            "YYYY-MM-DD"
                          )}
                          placeholder="HH:MM"
                          className="p-2"
                          {...register("dob", {
                            required: true,
                          })}
                        />
                        {errors.dob && (
                          <FieldError message={"This Field is Required"} />
                        )}
                      </Form.Group>
                    </Col>

                    <Col md={6}>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          Last Name
                        </FormLabel>
                        <Form.Control
                          type="text"
                          defaultValue={getPatientData?.lastName}
                          name="lastName"
                          placeholder="Smith"
                          className="p-2"
                          {...register("lastName", {
                            required: true,
                          })}
                        />
                        {errors.lastName && (
                          <FieldError message={"This Field is Required"} />
                        )}
                      </Form.Group>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          Gender
                        </FormLabel>
                        <Form.Select
                          name="genderId"
                          className="formSelect"
                          defaultValue={getPatientData?.genderId}
                          aria-label="Default select example"
                          {...register("genderId", {
                            required: true,
                          })}
                        >
                          {errors.genderId && (
                            <FieldError message={"This Field is Required"} />
                          )}
                          <option value={101}>Male</option>
                          <option value={102}>Female</option>
                          <option value={103}>Other</option>
                        </Form.Select>
                      </Form.Group>
                    </Col>

                    <Col xl={12}>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          Patient Local GP
                        </FormLabel>
                        <Form.Control
                          type="text"
                          name="patientLocalGP"
                          defaultValue={getPatientData?.patientLocalGP}
                          placeholder="Patient Local GP"
                          className="p-2"
                          {...register("patientLocalGP", {
                            required: true,
                          })}
                        />
                        {errors.patientLocalGP && (
                          <FieldError message={"This Field is Required"} />
                        )}
                      </Form.Group>
                    </Col>

                    <Col md={6}>
                      <Form.Group controlId="" className="mb-3">
                        <FormLabel className="fw-semibold fs-6">
                          Email
                        </FormLabel>
                        <Form.Control
                          type="email"
                          name="email"
                          value={user?.email}
                          placeholder="patient@gmail.com"
                          className="p-2"
                          disabled
                          // {...register("email", {
                          //   required: true,
                          // })}
                        />
                        {/* {errors.email && (
                        <FieldError message={"This Field is Required"} />
                      )} */}
                      </Form.Group>
                    </Col>
                    <Col md={6}>
                      <FormLabel className="fw-semibold fs-6">
                        Phone Number
                        <span style={{ color: "#FF3A3A" }}> *</span>
                      </FormLabel>
                      <PhoneInput
                        className="Phone-Number-FieldWidth border rounded w-100"
                        value={getPatientData?.phoneNumber}
                        onChange={(value) =>
                          setValue(`+${value}`)
                        }
                      />

                      <div className="position-relative verify-btn">
                        <Button
                          className="fs-5 primary_bg position-absolute bottom-0"
                          style={{ right: "0px" }}
                          size="sm"
                          variant="primary"
                          onClick={handleVerify}
                          disabled={isresendOtp?.data}
                        >
                          Verify
                        </Button>
                      </div>
                      {isresendOtp === null && (
                        <FieldError
                          message={"Phone Verification is Required"}
                        />
                      )}
                    </Col>

                    <Col md={6}>
                      <Form.Label
                        // className="Name-Label"
                        style={{
                          color: "#1A1A1A",
                          fontWeight: "bold",
                        }}
                      >
                        Next of Kin Name
                      </Form.Label>
                      <Form.Control
                        name="nextOfKinName"
                        type="text"
                        maxLength={100}
                        placeholder="Next of Kin Name"
                        className="p-2"
                        {...register("nextOfKinName")}
                        defaultValue={getPatientData?.nextOfKinName}
                      />
                      {errors.nextOfKinName && (
                        <FieldError message={"This Field is Required"} />
                      )}
                    </Col>
                    <Col md={6}>
                      <Form.Label
                        className=""
                        style={{
                          color: "#1A1A1A",
                          fontWeight: "bold",
                        }}
                      >
                        Next of Kin Number
                      </Form.Label>
                      <Form.Control
                        name="nextOfKinNumber"
                        type="text"
                        maxLength={100}
                        placeholder="Next of Kin Number"
                        className="p-2"
                        {...register("nextOfKinNumber")}
                        defaultValue={getPatientData?.nextOfKinName}
                      />
                      {errors.nextOfKinNumber && (
                        <FieldError message={"This Field is Required"} />
                      )}
                    </Col>
                    <Col md={6}>
                      <Form.Group controlId="" className="my-3">
                        <FormLabel className="fw-semibold fs-6">
                          Location
                        </FormLabel>
                        <Form.Control
                          type="text"
                          name="currentAddress"
                          defaultValue={getPatientData?.currentAddress}
                          placeholder="4140 Parker Rd. Allentown, New Mexico 31134"
                          className="p-2"
                          {...register("currentAddress", {
                            required: true,
                          })}
                        />
                        {errors.currentAddress && (
                          <FieldError message={"This Field is Required"} />
                        )}
                      </Form.Group>
                    </Col>
                  </Row>
                  <div className="d-flex justify-content-end w-100 mx-auto">
                    <Button
                      type="submit"
                      variant="primary"
                      className="px-3 fs-5 py-2 primary_bg"
                      size="sm"
                      disabled={isresendOtp === null}
                    >
                      Submit <AiOutlineRight className="fs-6" />{" "}
                    </Button>
                  </div>
                </Card.Body>
              </Card>
              <div className="py-3 d-flex justify-content-end width_75 mx-auto">
                <Button
                  onClick={() => navigate(PATH.MEDICAL_HISTORY)}
                  className="px-4 px-md-5 fs-5 py-2 primary_bg"
                  size="sm"
                  variant="primary"
                  // type="submit"
                >
                  Next <AiOutlineRight className="fs-6" />{" "}
                </Button>
              </div>
            </Form>
          )}
        </div>
      </ProfileLayout>
      <OTPCodeModal show={show} onClose={handleClose} />
    </div>
  );
};

export default PersonalInformation;
