import React, { useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { ClipLoader } from "react-spinners";
import { useForm } from "react-hook-form";
import { resendNumberOtp } from "../../../../app/features/phoneVerification/phoneVerification.slice";

export default function OTPCodeModal({ show, onClose }) {
  const { handleSubmit, register } = useForm();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const [otp, setOtp] = useState(["", "", "", ""]);
  const [seconds, setSeconds] = useState(60);
  const [intervalId, setIntervalId] = useState(null);
  const { isLoading } = useSelector((state) => state.auth);

  function handleChange(event, index) {
    const value = event.target.value;
    const newOtp = [...otp];
    newOtp[index] = value.replace(/\D/, "");
    setOtp(newOtp);
    if (index < 5 && value) {
      document.getElementById(`otp${index + 1}`)?.focus();
    }
  }

  function handleKeyDown(event, index) {
    if (event.key === "Backspace" && !otp[index] && index > 0) {
      document.getElementById(`otp${index - 1}`)?.focus();
    }
  }

  useEffect(() => {
    const id = setInterval(() => {
      setSeconds((prevSeconds) => prevSeconds - 1);
    }, 1000);
    setIntervalId(id);
    return () => clearInterval(id);
  }, []);

  useEffect(() => {
    if (seconds === 0) {
      // eslint-disable-next-line
      clearInterval(intervalId);
    }
  }, [seconds, intervalId]);

  const dispatch = useDispatch();

  const moveToNext = () => {
    onClose();
  };

  const onSubmit = (data) => {
    const concatenatedOtp = data.otp0 + data.otp1 + data.otp2 + data.otp3;

    const resendOtpData = {
      userId: user?.userId,
      OTP: concatenatedOtp,
    };
    dispatch(resendNumberOtp({ resendOtpData, moveToNext }));
  };

  return (
    <Modal show={show} onHide={onClose} size="md" backdrop="static">
      <Modal.Header closeButton>
        <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
          Verify Phone Number
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <form className="p-3" onSubmit={handleSubmit(onSubmit)}>
          <p className="text-center mb-5">
            Please enter the code below weâ€™ve sent to your phone number
          </p>
          {otp.map((digit, index) => (
            <input
              key={index}
              type="text"
              id={`otp${index}`}
              name={`otp${index}`}
              maxLength={1}
              value={digit}
              {...register(`otp${index}`, { required: true })}
              onChange={(event) => handleChange(event, index)}
              onKeyDown={(event) => handleKeyDown(event, index)}
              className="otp-field me-3 py-1 text-center"
              style={{
                width: "20%",
                fontweight: 500,
                fontSize: "31px",
              }}
            />
          ))}
          <Button
            className={`w-100 mt-5 ${isLoading && "disabled"}`}
            style={{ background: "#6045EB" }}
            type="submit"
          >
            {isLoading === true ? (
              <ClipLoader color="white" size={25} className="d-flex m-auto" />
            ) : (
              "Confirm Code"
            )}
          </Button>
        </form>
      </Modal.Body>
      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  );
}
